import { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { ImageUpload } from '../../components/admin/ImageUpload';
import { VideoUpload } from '../../components/admin/VideoUpload';
import { createCar, updateCar, getCar } from '../../lib/cars';
import type { Car } from '../../config/supabase';
import { useBrands } from '../../hooks/useBrands';
import { toast } from 'react-hot-toast';

const DEFAULT_FEATURES = [
  'Speed Regulator',
  'Air Condition',
  'Reversing Camera',
  'Reversing Radar',
  'GPS Navigation',
  'Park Assist',
  'Start and Stop',
  'Airbag',
  'ABS',
  'Computer',
  'Rims',
  'Electric mirrors',
  'Electric windows',
  'Bluetooth'
];

const FUEL_TYPES = ['Petrol', 'Diesel', 'Electric', 'Hybrid'];
const TRANSMISSION_TYPES = ['Automatic', 'Manual'];
const BODY_TYPES = ['Sedan', 'SUV', 'Coupe', 'Hatchback', 'Convertible', 'Van'];
const COLORS = ['Black', 'White', 'Silver', 'Blue', 'Red', 'Grey', 'Green', 'Brown', 'Beige'];

const initialFormData: Partial<Car> = {
  brand_id: '',
  make: '',
  model: '',
  year: new Date().getFullYear(),
  price: 0,
  image: '',
  video_url: '',
  savings: 0,
  condition: 'new',
  is_sold: false,
  mileage: '',
  fuel_type: 'Petrol',
  transmission: 'Automatic',
  autonomy: '',
  seats: 5,
  body_type: 'Sedan',
  exterior_color: 'Black',
  interior_color: 'Black',
  number_of_owners: 1,
  number_of_keys: '2'
};

export function CarForm() {
  const { id } = useParams();
  const navigate = useNavigate();
  const { data: brands, isLoading: brandsLoading } = useBrands();
  const [formData, setFormData] = useState(initialFormData);
  const [features, setFeatures] = useState<Set<string>>(new Set(DEFAULT_FEATURES));
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    if (id) {
      loadCar(id);
    }
  }, [id]);

  async function loadCar(carId: string) {
    try {
      setIsLoading(true);
      const car = await getCar(carId);
      if (car) {
        const { id: _, created_at, updated_at, brand, images, features: carFeatures, ...carData } = car;
        setFormData(carData);
        if (carFeatures) {
          setFeatures(new Set(carFeatures.filter(f => f.available).map(f => f.name)));
        }
      }
    } catch (err) {
      console.error('Error loading car:', err);
      setError('Failed to load car details');
      toast.error('Failed to load car details');
    } finally {
      setIsLoading(false);
    }
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);
    setError(null);

    try {
      if (!formData.brand_id) {
        throw new Error('Please select a brand');
      }

      const carData = {
        ...formData,
        features: Array.from(features).map(name => ({
          name,
          available: true
        }))
      };

      if (id) {
        await updateCar(id, carData);
        toast.success('Car updated successfully');
      } else {
        await createCar(carData as Required<Omit<Car, 'id' | 'created_at' | 'updated_at' | 'brand'>>);
        toast.success('Car created successfully');
      }
      navigate('/admin/dashboard/cars');
    } catch (err) {
      console.error('Error saving car:', err);
      const message = err instanceof Error ? err.message : 'Failed to save car. Please try again.';
      setError(message);
      toast.error(message);
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>) => {
    const { name, value, type } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: type === 'number' ? Number(value) : value || ''
    }));

    if (name === 'brand_id') {
      const selectedBrand = brands?.find(b => b.id === value);
      if (selectedBrand) {
        setFormData(prev => ({
          ...prev,
          brand_id: value,
          make: selectedBrand.name
        }));
      }
    }
  };

  const handleFeatureToggle = (feature: string) => {
    setFeatures(prev => {
      const newFeatures = new Set(prev);
      if (newFeatures.has(feature)) {
        newFeatures.delete(feature);
      } else {
        newFeatures.add(feature);
      }
      return newFeatures;
    });
  };

  if (isLoading || brandsLoading) {
    return (
      <div className="flex justify-center items-center h-64">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold text-gray-900 mb-6">
        {id ? 'Edit Car' : 'Add New Car'}
      </h1>

      {error && (
        <div className="mb-6 bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded relative">
          {error}
        </div>
      )}

      <form onSubmit={handleSubmit} className="space-y-8">
        {/* Rest of the form JSX remains the same */}
      </form>
    </div>
  );
}